<template>
  <div>
    <!--图片略缩容器-->
    <div class="viwer-box">
      <div class="viwer-warpper rectangular" v-for="(item,index) in pictureItems" :key="index">
        <img :src="$cms.filterUrl(item.url)" class="thumb-img">
        <div class="thumb-cover">
          <i class="el-icon-zoom-in" @click.stop='showViewer'></i>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'thumbImg',
  props: {
    value: [String, Number],
    params: {
      default: function() {
        return {}
      },
      type: Object
    },
    data: { // 数据结构参考资源的数据结构
      type: [Array, Object]
    },
    props: {// 默认的属性名，如果不同穿这个
      default: function() {
        return {
          id: 'id',
          url: 'url'
        }
      },
      type: Object
    }
  },
  data() {
    return {
      state: false, // 显示隐藏,
      resource: {
        // 资源文件对象
        name: '全部图片',
        id: ''
      },
      pictureItems: []
    }
  },
  methods: {
    showViewer() {
      this.$viewer({
        list: this.data
      })
    },
    deleteItems(index) {
      // 删除对应的数据项
      this.pictureItems.splice(index, 1)
      if (this.pictureItems.length > 0) {
        this.$emit('change', this.pictureItems, this.index, this.params)
        this.$emit('input', this.pictureItems[0].id)
      } else {
        this.$emit('change', null, this.index, this.params)
        this.$emit('input', '')
      }
    }
  },
  watch: {
    data: {
      handler() {
        if (this.$cms.validateObjcet(this.data) === 'object') {
          this.pictureItems = []
          if (this.data[this.props.id] && this.data[this.props.id] !== '') {
            this.pictureItems.push(this.data)
          }
        } else {
          this.pictureItems = this.data
        }
      },
      deep: true,
      immediate: true
    }
  }
}
</script>

<style scoped lang='scss'>
.cms-zone-warper {
  overflow: hidden;
}
.viwer-box {
  overflow: hidden;
  max-width: 750px;
}
.viwer-warpper {
  float: left;
  position: relative;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 5px;
  margin-right: 10px;
  width: 90px;
  height: 90px;
  border: 1px solid #e7e7e7;
  cursor: pointer;
  &:hover {
    .thumb-cover {
      display: flex;
    }
  }
}

.rectangular {
  width: 50px;
  height: 50px;
}
.thumb-img {
  display: block;
  width: auto;
  height: auto;
  max-width: 100%;
  max-height: 100%;
}
.thumb-cover {
  display: none;
  position: absolute;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  top: 0;
  left: 0;
  text-align: center;
  align-items: center;
  justify-content: center;
  i {
    display: block;
    width: 35px;
    cursor: pointer;
    font-size: 22px;
    color: #fff;
  }
}

</style>



