<template>
 <div>
    <cms-breadcrumb to="/comment/list" type='list'>
      <li>{{$route.name}}</li>
    </cms-breadcrumb>
    <div class="cms-list-layout">
    <!-- 数据列表 -->
    <table class="comment-table" v-loading="loading" v-if="!loading">
      <tbody>
        <tr class="comment-th">
          <td style="padding-left:10px;">
            <div v-if="!loading&&formData.order">
              <span class="comment-status" v-if="formData.order.commentStatus">
                {{formData.order.commentStatus==2?'已评价':'双方已评'}}
              </span>
              <span>{{formData.orderCode}}</span>
            </div>
          </td>
          <td class="comment-time">
            <span style="margin-left:10px;">商品评价</span>
             <p>
              <span style="margin-right:80px">评价人：{{formData.memberName}}</span>
              <span style="margin-right:10px;">评论时间：{{$moment(formData.createTime)}}</span>
            </p>
          </td>
        </tr>
        <tr style="border:1px solid #EBEEF5;"  v-for="(info,indexs) in formData.commentdList" :key="indexs">
          <td class="comment-goods">
            <!-- 商品信息 -->
            <div class="comment__inner">
              <img class="product-img" :src="$cms.filterUrl(info.product.coverImage)">
              <div class="product-title">
                <h3 class="title-h">{{info.product.name}}</h3>
                <p class="title-p" style="margin-top:5px;" v-if="info.productSkuName">
                  规格:{{info.productSkuName}}</p>
              </div>
            </div>
         </td>
         <td class="comment-item">
           <!-- 商品评价 -->
           <div class="comment-remark"
             style="display:flex;border-bottom:1px solid #EBEEF5">
             <div>
               <el-rate v-model="info.matchV" disabled :show-text="true" :texts="starsText"></el-rate>
               <p class="comment-title" :class="showColors(info.matchV)">{{info.content}}</p>
             </div>
             <div style="width:50%">
               <div class="comment-imgs">
                 <thumb-img :data="info.spaceDataList"></thumb-img>
               </div>
             </div>
           </div>
          <div class="comment-remark">
             <div style="width:50%">
               <p style="margin-bottom:5px;font-size:14px;">您的回复:</p>
               <el-input type="textarea" v-model="info.storeContent" :rows="3"
                placeholder="请填写您对买家的回复吧,不超过500字" maxlength="500">
               </el-input>
             </div>
           </div>
         </td>
        </tr>
        <!-- 总体评价 -->
        <tr class="comment-all">
          <td colspan="2">
            <div style="display: flex;margin-left:10px;align-items:center;">
              <span>店铺评价</span>
              <div class="comment-stars">
                <label class="stars-label">商品符合度:</label>
                <el-rate v-model="formData.matchV" :show-text="true" class="starts-rate" disabled
                :texts="starsText">
                </el-rate>
                <label class="stars-label">发货速度:</label>
                <el-rate v-model="formData.express" :show-text="true" class="starts-rate" disabled
                 :texts="starsText">
                </el-rate>
                <label class="stars-label">服务态度:</label>
                <el-rate v-model="formData.service" :show-text="true" class="starts-rate" disabled
                 :texts="starsText">
                </el-rate>
              </div>
            </div>
          </td>
        </tr>
      </tbody>
    </table>
    <div style="text-align:right;margin-top:30px;width:48%;">
      <cms-button size="small" :colorPicker='true' @click="update">
        {{formData.order.commentStatus==2?'发表回复':'修改回复'}}
      </cms-button>
    </div>
  </div>
 </div>
</template>

<script>
import form from '@/mixins/form'
import thumbImg from './thumbImg'
import { commentGet, commentUpdate } from '@/api/customer/customer.js'
export default {
  mixins: [form],
  components: {
    thumbImg
  },
  data() {
    return {
      formData: {},
      loading: false,
      starsText: ['差评', '差评', '中评', '好评', '好评']
    }
  },
  methods: {
    getFormData(id) {
      this.loading = true
      commentGet({ id: id }).then(res => {
        if (res.code === 200) {
          this.formData = res.data
        } else {
          this.$errorMessage(res.message)
        }
        this.loading = false
      })
    },
    showColors(val) { // 根据不同评分值来显示字体的颜色
      if (val <= 2) {
        return 'fontRed'
      } else if (val === 3) {
        return 'fontBlue'
      } else {
        return ''
      }
    },
    update() {
      const commentdList = []
      this.formData.commentdList.forEach(v => {
        const temObj = {
          id: v.id,
          storeContent: v.storeContent
        }
        commentdList.push(temObj)
      })
      const params = {
        id: this.formData.id,
        express: this.formData.express,
        matchV: this.formData.matchV,
        service: this.formData.service,
        commentdList: commentdList
      }
      commentUpdate(params).then(res => {
        if (res.code === 200) {
          if (this.formData.order.commentStatus === 2) {
            this.$successMessage('发表成功')
          } else {
            this.$successMessage('修改成功')
          }
          this.$routerLink('/comment/list', 'list')
        } else {
          this.$errorMessage(res.message)
        }
      })
    }
  },
  created() {
    this.getFormData(this.$route.query.id)
  }
}
</script>
<style scoped>
.comment-table{
  width: 100%;
  border: 1px solid #EBEEF5;
  padding: 0 10px;
  color: rgb(96, 98, 102);
  border-collapse: collapse;
  font-size: 12px;
}
.comment-th{
  background-color: #f7f7f7;
  text-align: left;
  height: 40px;
  line-height: 40px;
  font-size: 14px;
  font-weight: normal;
}
.title-h{
  font-size: 14px;
  color: #333;
  overflow: hidden;
  max-height: 40px;
  overflow: hidden;
  text-overflow: ellipsis;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
   width: 340px;
  display: -webkit-box;
  margin-bottom: 4px;
  font-weight: normal;
}
.comment-goods{
  width: 40%;
  padding: 10px 0;
  border-right:1px solid #EBEEF5;
  vertical-align: top;
}
.comment-item{
  width: 60%;
  vertical-align: top;
}
.comment-imgs{
  display: flex;
  justify-content: flex-end;
}

.comment-time{
  display: flex;
  justify-content: space-between;
}
.comment-remark{
 padding: 10px 0;
 min-height: 80px;
 padding-left: 10px;
}
.comment-status{
  display: inline-block;
  min-width: 60px;
  height: 24px;
  color:#69B469;
  font-size: 12px;
  border: 1px solid #69B469;
  text-align: center;
  line-height: 24px;
  border-radius: 3px;
  margin-right: 5px;
}
.comment-title{
  font-size: 14px;
  max-width: 360px;
  overflow-wrap: break-word;
  line-height: 20px;
}
.commnet-arrow{
  font-size:14px;
  font-weight:900;
  cursor:pointer;
}
.starts-rate{
  margin-right: 40px;
}
.comment-stars{
  display: flex;
  margin-left: 20px;
  align-items: center;
}
.stars-label{
  margin-right: 5px;
}
.fontRed{
  color: #F56C6C;
}
.fontBlue{
  color: #409EFF;
}
.comment-all{
  height: 40px;
}
</style>




